import React, { Component } from "react";
import { Button, Col, Row, Form } from "react-bootstrap";
import history from '../history'

// ... REDUX ...
import withReducer from "../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "./store/reducers";
import * as Actions from "./store/actions";


import Social from './authorization/social/Social'
import LoginForm from './authorization/login/LoginForm'
import RegisterForm from './authorization/register/RegisterForm'

class LoginPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      offers: false,   
      isAuthenticated: 1,
    };
  }

  // ... HANDLERS ...   
  componentDidMount() {
    if(localStorage.getItem('jwtToken'))
    {
      this.props.showResponseMessage('You are already logged in...','danger')
      history.push({
        pathname: '/'
      })
    }
  }

  responseGoogleFailiure = response => {
    console.log (response); 
  }

  static getDerivedStateFromProps(props, state) {
    // console.log('User' + props.user);
        if(props.user != null)
        {
          history.push({
            pathname: '/',
            state: {reRender:3}
          })
        }
        else if (props.user == null) 
        {
          return {
            msg: props.msg
          }
        }
  }

  render() {
    let {user} = this.props;

    // console.log('USER :: ' + JSON.stringify(user));

    return (
      <>
        {/* <Header /> */}
        <section className="login-page">
          <div className="content">
            <div className="header">
              <span>
                {" "}
                Login / <b>Register</b>
              </span>
            </div>
            <div className="offer">
              <div className="d-flex">
                <span className="bg" />
                <span className="text">
                  Register or login now to <b>get rs. 2500</b> worth of cash
                  coupons in your account to begin shopping{" "}
                </span>
              </div>
            </div>
            <Row className="m-0 pb-5">
              <Col md={0} lg={1} className="p-0">
                <div className="sub-title mb" />
              </Col>
              <Col md={6} lg={2} className="p-sm-3 p-lg-0">
                <div className="sub-title">Existing customer login</div>
                {/* LOGIN FORM  */}
                  <LoginForm/>                
              </Col>
              <Col md={6} lg={3} className="p-m-0 p-lg-0 p-sm-3">
                <div className="sub-title ml-lg-0 pl-lg-5">
                  New Customer SignUp
                </div>
                {/* SIGNUP FORM  */}                
                <RegisterForm/>
              </Col>
              <Col md={6} lg={6} className="p-m-0 p-lg-0 p-sm-3 ">
                <div className="sub-title social">Social Plugins</div>
                <div className="d-flex">
                  <div className="or">
                    <span>OR</span>
                  </div>
                  <div class="d-flex flex-column pl-5 ml-5 ">
                    <div className="d-flex flex-column social-login1">
                      <span className="d-block pb-4">
                          Use your Social accounts to
                          <br /> Signup / Login
                      </span>
                      <Social/>
                    </div>
                  </div>
                </div>
              </Col>
            </Row>
          </div>
        </section>{" "}

        {/* <Footer /> */}
      </>
    );
  }
}
  
  function mapStateToProps(state) {
  return {
      isAuthenticated: state.authentication.auth.isAuthenticated,
      user_object: state.authentication.auth.user_object,
    };
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return bindActionCreators({
    showResponseMessage: Actions.showResponseMessage
  }, dispatch)
}

export default withReducer('authentication' , reducer)
              (connect(
                mapStateToProps,
                mapDispatchToProps)
                (LoginPage));

